body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.todos {
  width: 500px;
  margin: 0 auto;
  font-size: 30px;
}

.add-todo {
  position: relative;
}

.add-todo .new-todo {
  display: block;
  margin: 0 0 0 50px;
  font-size: 25px;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  font-smoothing: antialiased;
}

.add-todo .add-btn {
  font-size: 22px;
  position: absolute;
  top: 0;
  right: 0;
}

.todo-list {
  list-style: none;
  /* margin: 0;
  padding: 0; */

  font-size: 22px;
  padding-left: 50px;
  margin-top: 20px;
}


.todo-item {
  position: relative;
  height: 60px;
  line-height: 50px;
  vertical-align: middle;
  text-align: left;
}

.todo-item .toggle {
  height: 40px;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  position: absolute;
  top: 0;
  bottom: 0;
}

.todo-item .toggle:after {
  margin: 10px 0 0 0;
	position: absolute;
	width: 40px;
	height: 40px;
	background: green;
	color: #fff;
	left: 0;
	top: 0;
	margin-top: 0;
  content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>');
}

.todo-item .toggle:checked:after {
	position: absolute;
	width: 40px;
	height: 40px;
	background: red;
	color: #fff;
	left: 0;
	top: 0;
	margin-top: 0;
  content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>');
}

.todo-item .text {
  margin: 0 0 0 50px;
  height: 40px;
}

.todo-item .remove {
  border: 0;
  background: 0;
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  width: 40px;
  height: 40px;
  font-size: 30px;
  color: #cc9a9a;
  margin-bottom: 11px;
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
}

.todo-item:hover .remove {
  display: block
}

.todo-item .remove:hover {
  color: #af5b5e;
}
.filters {
    font-size: 20px;
    width: 500px;
    margin: 0 auto;
    text-align: left;
    padding-left: 50px;
}

.filters .filter {
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 3px;
}

.filters .selected {
  border: 1px solid transparent;
  text-decoration: none;
}

.filters .not-selected {
  color: black;
  text-decoration: none;
  border: 1px solid;
  border-color: rgba(175, 47, 47, 0.2);
}

.filters .not-selected:hover {
  border-color: rgba(175, 47, 47, 0.2);
}
.App {
  text-align: center;
  padding-bottom: 50px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 20vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 40vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.isLike{
  padding: 5vh 0 ;
}
span.bold{
  font-weight: bold;
  font-size: calc(10px + 3vmin)
}
.warning{
  padding: 5vh;
  margin: 3vh 0;
  background-color: yellow;
}
.am-button{
  overflow: inherit;
  margin-right: 10px;
  height: 35px;
  line-height: 35px;
}
.link-list{
  margin-top: 10px;
}
.link-list:after{
  clear:both;
}
.link-list>a{
  color: #333;
  padding: 6px 12px;
  margin-right: 5px;
  border: 1px solid #ddd
}
.link-list>a.selected{
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

